import React, { useEffect, useState } from 'react';
import GotService from '../../services';
import './randomChar.css';
import Spinner from '../spinner';
import ErrorMessage from '../error';

export default function RandomChar() {

    const gotService = new GotService()

    const [char, charUpdate] = useState({})
    const [error, errorUpdate] = useState(false)
    const [loading, loadingUpdate] = useState(true)

    useEffect(() => {
        updateCharacter()
        let timerId = setInterval(updateCharacter, 10000)
        return () => {
            clearInterval(timerId)
        }
    }, [])

    const onCharLoaded = (char) => {
        charUpdate(char)
        loadingUpdate(false)
    }

    const onError = (err) => {
        errorUpdate(true)
        loadingUpdate(false)
    }

    const updateCharacter = () => {
        const id = Math.floor(Math.random() * 150 + 25)
        gotService.getCharacter(id)
            .then(onCharLoaded)
            .catch(onError)
    }

    const errorMessage = error ? <ErrorMessage /> : null
    const spinner = loading ? <Spinner /> : null
    const content = !(loading || error) ? <View char={char} /> : null

    return (
        <div className="random-block rounded">
            {errorMessage}
            {spinner}
            {content}
        </div>
    )
}


const View = ({ char }) => {
    const { name, gender, born, died, culture } = char
    return (
        <>
            <h4>Random Character: {name}</h4>
            <ul className="list-group list-group-flush">
                <li className="list-group-item d-flex justify-content-between">
                    <span className="term">Gender </span>
                    <span>{gender}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className="term">Born </span>
                    <span>{born}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className="term">Died </span>
                    <span>{died}</span>
                </li>
                <li className="list-group-item d-flex justify-content-between">
                    <span className="term">Culture </span>
                    <span>{culture}</span>
                </li>
            </ul>
        </>
    )
}






