import React from "react"
import './service.css'

export default class GotService {
    constructor() {
        this._apiBase = 'https://www.anapioficeandfire.com/api/'
    }

    getResource = async (url) => {
        const res = await fetch(`${this._apiBase}${url}`)

        if (!res.ok) {
            throw new Error(`Could not fetch ${url}, status: ${res.status}`)
        }

        return await res.json()
    }

    getAllCharacters = async () => {
        const res = await this.getResource(`characters/?page=29&pageSize=10`)
        return res.map(this._transformCharacter)
    }

    getCharacter = async (id) => {
        const character = await this.getResource(`characters/${id}`)
        return this._transformCharacter(character)
    }

    getAllBooks = async () => {
        const books = await this.getResource('books')
        return books.map(this._transformBook)
    }

    getBook = async (id) => {
        const book = await this.getResource(`books/${id}`)
        return this._transformBook(book)
    }

    getAllHouses = async () => {
        const houses = await this.getResource('houses')
        return houses.map(this._transformHouse)
    }

    getHouse = async (id) => {
        const house = await this.getResource(`houses/${id}`)
        return this._transformHouse(house)
    }

    _extractId = (item) => {
        const idRegExp = /\/([0-9]*)$/;
        return item.url.match(idRegExp)[1];
    }

    _transformCharacter = (char) => {
        return {
            name: char.name ? char.name : <DataError />,
            gender: char.gender ? char.gender : <DataError />,
            born: char.born ? char.born : <DataError />,
            died: char.died ? char.died : <DataError />,
            culture: char.culture ? char.culture : <DataError />,
            id: this._extractId(char)
        }
    }

    _transformHouse = (house) => {
        return {
            name: house.name ? house.name : <DataError />,
            region: house.region ? house.region : <DataError />,
            words: house.words ? house.words : <DataError />,
            titles: house.titles ? house.titles : <DataError />,
            overlord: house.overlord ? house.overlord : <DataError />,
            ancestralWeapons: house.hasAncestralWeapons	 ? house.hasAncestralWeapons : <DataError />,
            id: this._extractId(house)
        }
    }

    _transformBook = (book) => {
        return {
            name: book.name ? book.name : <DataError />,
            numberOfPages: book.numberOfPages ? book.numberOfPages : <DataError />,
            publisher: book.publisher ? book.publisher : <DataError />,
            released: book.released ? book.released : <DataError />,
            country: book.county ? book.county : <DataError/>,
            authors: book.authors ? book.authors : <DataError/>,
            id: this._extractId(book)
        }
    }
}

const DataError = () => {
    return (
        <>
            <span className="red">NO_DATA</span>
        </>
    )
}

