import './app.css'
import React, { Component } from 'react';
import { Col, Row, Container, Button } from 'reactstrap';
import Header from '../header';
import RandomChar from '../randomChar';
import ErrorMessage from '../error';
import CharacterPage from '../pages/characterPage';
import GotService from '../../services';
import HousePage from '../pages/housesPage';
import BookPage from '../pages/booksBage';
import BooksItem from '../pages/booksBage/booksItem';

import { BrowserRouter as Router, Route, Routes, useParams } from 'react-router-dom';



export default class App extends Component {
    constructor() {
        super()

        this.state = {
            randomChar: true,
            error: false
        }
        this.btnClick = this.btnClick.bind(this)
    }

    gotService = new GotService()

    componentDidCatch() {
        this.setState({
            error: true
        })
    }

    btnClick() {
        this.setState({
            randomChar: !this.state.randomChar
        })
    }



    render() {
        if (this.state.error) {
            return <ErrorMessage />
        }


        return (
            <Router>
                <div className='app'>
                    <Container>
                        <Header />
                    </Container>
                    <Container>
                        <Row>
                            <Col className='transit' lg={{ size: 5, offset: 0 }}>
                                {this.state.randomChar ? <RandomChar /> : null}
                            </Col>
                            <Col lg={{ size: 5, offset: 0 }}>
                                <Button className='mb-20' color="info" outline onClick={this.btnClick}>
                                    {this.state.randomChar ? 'Hide random char' : 'Show random char'}
                                </Button>
                            </Col>
                        </Row>

                        <Routes>
                            <Route path='/characters' element={<CharacterPage />} />
                            <Route path='/houses' element={<HousePage />} />
                            <Route path='/books' exact element={<BookPage />} />
                            <Route path='/books/:id' element={<BooksItem />} />
                        </Routes>

                    </Container>
                </div>
            </Router>
        );
    }
};

