import React, { useState, useEffect } from 'react';
import './itemDetails.css';
import Button from 'reactstrap/lib/Button';
import { Link } from 'react-router-dom';

const Field = ({ item, field, label }) => {
    return (
        <li className="list-group-item d-flex justify-content-between">
            <span className="term">{label}</span>
            <span>{item[field]}</span>
        </li>
    )
}

export { Field }

export default function ItemDetails({ children, itemId, book, getData }) {
    const [item, changeItem] = useState(null)

    useEffect(() => {
        updateChar()
    }, [])

    useEffect(() => {
            updateChar()
            console.log(item);
    }, [itemId] )

    function updateChar() {
        if (!itemId) {
            return
        }

        getData(itemId)
            .then(item => {
                changeItem(item)
            })
    }

    if (!item) {
        return <span className='select-error'>Click on item!</span>
    }

    const Book = () => {
        if (book) { return (<Button><Link to='/books'>Back to list</Link></Button>) }
        else { return null }
    }

    const { name } = item

    return (
        <div className="item-details rounded">
            <Book />
            <h4>{name}</h4>
            <ul className="list-group list-group-flush">
                {
                    React.Children.map(children, (child) => {
                        return React.cloneElement(child, { item })
                    })
                }
            </ul>
        </div>
    );
}